import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  placedDate: PropTypes.string.isRequired,
  status: PropTypes.string.isRequired,
  code: PropTypes.string.isRequired,
  facilityId: PropTypes.number,
});

export const formValues = PropTypes.shape({
  placedDate: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  status: PropTypes.string,
  code: PropTypes.string,
  facilityId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
});

export const formTouched = PropTypes.shape({
  placedDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  status: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  code: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  facilityId: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  placedDate: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  status: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  code: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  facilityId: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
