import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/product-packages';

export const apiProductPackageGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiProductPackagePost = async (serviceUrl, productPackage) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: productPackage ? JSON.stringify(productPackage) : null,
  };
  return request(url, options);
};

export const apiProductPackagePut = async (serviceUrl, productPackage) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: productPackage ? JSON.stringify(productPackage) : null,
  };
  return request(url, options);
};

export const apiProductPackageDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
